/* preview-carousel */
.products__list {
	margin-left: -15px;
	margin-right: -15px;
}

.products__item {
	float: left;
	width: 33.33%;
	padding-left: 15px;
	padding-right: 15px;
}

.products__item:nth-child(n) {
	margin-top: 30px;
	clear: none;
}

.products__item:nth-child(-n+3) {
	margin-top: 0;
}

.products__item:nth-child(3n+1) {
	clear: left;
}

@media (max-width: 991px) {
	.products__item {
		width: 50%;
		padding-left: 20px;
		padding-right: 20px;
	}
	.products__item:nth-child(n) {
		margin-top: 60px;
		clear: none;
	}
	.products__item:nth-child(-n+2) {
		margin-top: 0;
	}
	.products__item:nth-child(2n+1) {
		clear: left;
	}
}

.products__inner {
	position: relative;
	display: block;
}

.products__img, .products__gallery {
	position: relative;
}

.products__img::before, .products__gallery::before {
	content: "";
	display: block;
	height: 0;
	padding-top: 100%;
}

.products__img img, .products__gallery img {
	object-fit: cover;
	font-family: "object-fit: cover";
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	max-width: none;
}

.products__gallery {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	z-index: 3;
	width: 100%;
	display: none;
}

.products__inner:hover .products__gallery {
	display: block;
}

.tile-slider {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
}

.tile-slider__item {
	position: static;
	display: flex;
	align-items: flex-end;
	flex: 1;
	height: 100%;
}

.tile-slider__img, .tile-slider__figure {
	position: absolute;
	left: 0;
	width: 100%;
	height: auto;
	border: 0;
}

.tile-slider__figure {
	z-index: -2;
	top: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
}

.tile-slider__item .tile-slider__figure {
	opacity: 0;
}

.tile-slider:not(:hover) .tile-slider__item:first-child .tile-slider__figure, .tile-slider__item:hover .tile-slider__figure {
	z-index: -1;
	opacity: 1;
}

.tile-slider__more {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	text-align: center;
	color: #fff;
	background-color: rgba(0, 0, 0, 0.5);
}

.tile-slider__more-holder {
	position: absolute;
	top: 50%;
	left: 50%;
	text-align: center;
	transform: translate(-50%, -50%);
}

.tile-slider__more-icon {
	width: 54px;
	height: 46px;
	margin: 0 auto;
	background: url(data:image/svg+xml;base64,);
}

.tile-slider__more-text {
	font-size: 13px;
	line-height: 17px;
	margin-top: 13px;
	color: #fff;
}

.tile-slider__dots {
	position: relative;
	z-index: 4;
	bottom: 0;
	flex: 1;
	min-height: 7px;
	opacity: 0;
	will-change: opacity;
	transition-property: opacity;
	transition-duration: 0.2s;
}

.tile-slider:hover .tile-slider__dots {
	opacity: 1;
}

.tile-slider__dots::after {
	content: "";
	position: absolute;
	top: 1px;
	right: 1px;
	bottom: 2px;
	left: 1px;
	height: 3px;
	background: rgba(255, 255, 255, 0.8);
	transition-property: background-color;
	transition-duration: 0.2s;
}

.tile-slider__item:hover .tile-slider__dots:after {
	background-color: #db3727;
}

/* preview-carousel end */
